<template>
    <div id="template">
        <v-btn  id="print" class="blue white--text mr-4 mb-5" @click="printReport()">Print</v-btn>
        <v-btn id="print" class="red white--text mb-5" to="/cetakmrbesar" link>Back</v-btn>
        <p id="print">Catatan : <br> *Kertas Print = 155 x 250 mm <br> *Orientasi = Portrait</p>
        <template id="template">
            <div class="grid-container">
                <template v-for="i in karton">    
                    <div v-for="j in i['Jumlah']" :key="j.id" class="grid-item">
                        <table style="height: 4.898cm;width: 3cm;">
                            <tr>
                                <td colspan="2" style="vertical-align: top;">
                                    <img class="qr" :src="$publicUrl + i['pathQR']" alt="Barcode" >
                                </td>
                            </tr>
                            <tr>
                                <td style="vertical-align: top;">
                                    <img class="barcode" style="width: 0.651cm; height: 2.7cm;" :src="$publicUrl + i['path']" alt="Barcode" >
                                </td>
                                <td style="vertical-align: top;">
                                    <div style="color: #000080;font-family: Arial, Helvetica, sans-serif;font-size: 5pt;font-weight: bold;margin-top: 1.49900cm;margin-left: 0.201cm;">Ingredients:</div>
                                    <div style="color: #000080;font-family: Arial, Helvetica, sans-serif;font-size: 5pt;font-weight: bold;margin-left: 0.201cm;vertical-align: top;height:0.852cm">{{ i['Ingredients']}}</div>
                                    <div style="color: #000080;font-family: Arial, Helvetica, sans-serif;font-size: 5pt;font-weight: bold;margin-left: 0.201cm;">Nama Brg : {{ i['Kode Barang']}}</div>
                                    <div style="font-family: Arial, Helvetica, sans-serif;font-size: 4pt;font-weight: bold;margin-top: 0.019cm;margin-left: 0.201cm;">No Bets : {{ i['No Bets']}}</div>
                                    <div style="color: #FF0000;font-family: Arial, Helvetica, sans-serif;font-size: 4pt;font-weight: bold;margin-top: 0.078cm;margin-left: 0.201cm;">Exp Date : {{ i['Expired Date']}}</div>
                                    <div style="font-family: Arial, Helvetica, sans-serif; font-size: 4pt;font-weight: bold;margin-top: 0.058cm;margin-left: 0.201cm;">Netto : {{ i['Isi']}} ml</div>
                                    <div style="font-family: Arial, Helvetica, sans-serif;font-size: 4pt;font-weight: bold;margin-top: 0.04cm;margin-left: 0.1cm; width:2.199cm;">BPOM RI {{ i['NoEdar']}}</div>
                                </td>
                            </tr>
                        </table>
                    </div>
                </template>
            </div>
        </template>
    </div>
</template>
<script>
    export default { 
        data() {
            return {
                karton: [],
            }
        },
        methods: {
            getData() {
                var uri = this.$apiUrl + 'cetakmr/cetak'
                this.$http.get(uri).then(response => {
                    this.karton = response.data
                    console.log(this.karton)
                })
            },

            printReport() {
                window.print()
            },

            Activity(key) {
                // untuk memposting aktivitas user yang login
                var activity = new FormData
                activity.append('activity', key)
                var uri = this.$apiUrl + 'activity'
                this.$http.post(uri, activity).then(response => {}).catch(error => {
                    this.errors = error
                })
            },
        },
        mounted() {
            this.getData();
        },
    }
</script>
<style scoped>
    @media print {
        #print {
            display: none;
        }

        #template {
            background-color: white
        }
        /* html, body {
                width: 152mm;
                height: 120mm;
            } */
            /* .qr{
                margin-left: 2.101cm;
            }
            .barcode{
                 margin-top: 1.3cm;
                 
            } */

    }
   .grid-container {
            display: grid;
            /* grid-template-columns: 3cm 3cm 3cm 3cm; */
            grid-template-columns: repeat(3, 3cm);
            grid-gap: 1.79cm 0.504cm;
            margin: 8.01mm 7mm 7mm 11.99mm;
        }
        .qr{
            margin-left: 2.101cm;
            width: 0.7cm;
            height: 0.7cm;
            -webkit-print-color-adjust: exact; 
        }
        .barcode{
            margin-top: 1.3cm;
            -webkit-print-color-adjust: exact;
            
        }
        
